/*----------------------СТИЛЬ АДАПТИВА---------------------------*/
.wrapper {
	display: flex;
	min-height: 100vh;
	overflow: hidden;
	flex-direction: column;
}
.container {
	max-width: 1200px;
	margin: 0 auto;
	padding: 0 15px;
}

@media (max-width: 1200px) {
	.container {
		max-width: 970px;
	}
}
@media (max-width: 992px) {
	.container {
		max-width: 750px;
	}
}
@media (max-width: 767px) {
	.container {
		max-width: none;
	}
}
